
.dropload-up,.dropload-down{position:relative;height:0;overflow:hidden;font-size:12px;transform:translateZ(0)
}
.dropload-down{height:50px
}
.dropload-refresh,.dropload-update,.dropload-load,.dropload-noData{height:50px;line-height:50px;text-align:center
}
.dropload-load .loading{display:inline-block;height:15px;width:15px;border-radius:100%;margin:6px;border:2px solid #666;border-bottom-color:transparent;vertical-align:middle;animation:rotate .75s linear infinite
}
@keyframes rotate{
0%{transform:rotate(0deg)
}
50%{transform:rotate(180deg)
}
100%{transform:rotate(360deg)
}
}
.header{position:inherit;width:100%
}
.nav{height:35px;border-bottom:1px solid #ccc;width:100%;background:#fff
}
.nav .swiper-slide,.nav .swiper-wrapper{width:auto;height:100%;line-height:35px;padding-left:10px
}
.fiexBox{position:fixed;top:0;left:0;width:100%
}
.sortWrap{width:100%;height:35px;border-bottom:1px solid #ccc;background:#fff
}
.content{margin-top:115px;padding:10px 0
}
.sortWrap .active{color:#ff9600
}
.nav .active{color:#ff9600
}


