
#loading{
	background-color: #ffffff;
	height: 100%;
	width: 100%;
	position: fixed;
	z-index: 99999999999999;
	margin-top: 0px;
	top: 0px;
}
#loading-center{
	width: 100%;
	height: 100%;
	position: relative;
}
#loading-center-absolute {
	position: absolute;
	left: 50%;
	top: 50%;
	height: 50px;
	width: 150px;
	margin-top: -25px;
	margin-left: -75px;
}
.object{
	width: 8px;
	height: 50px;
	margin-right:5px;
	background-color: rgba(0, 0, 0, .5);
	animation: animate 1s infinite;
	float: left;
}
.object:last-child {
	margin-right: 0px;
}
.object:nth-child(10){
 animation-delay: 0.9s;
}
.object:nth-child(9){
 animation-delay: 0.8s;
}
.object:nth-child(8){
 animation-delay: 0.7s;
}
.object:nth-child(7){
 animation-delay: 0.6s;
}
.object:nth-child(6){
 animation-delay: 0.5s;
}
.object:nth-child(5){
 animation-delay: 0.4s;
}
.object:nth-child(4){
 animation-delay: 0.3s;
}
.object:nth-child(3){
 animation-delay: 0.2s;
}
.object:nth-child(2){
 animation-delay: 0.1s;
}
@keyframes animate {
50% {
 transform: scaleY(0);
}
}




