
.good{
   	  height: 1000px;
}
.swiperImg{
    width: 100%;
    overflow: hidden;
}
.swiperImg img{
        display: block;
        width: 100%;
        height: auto;
}
.sp-dl{
   	    width: 33.33%;
        float: left;
}
.sp-dl dt {
    text-align: center;
}
.sp-dl dt img{
	    width: 74px;
    height: 74px;
    display: block;
    margin: 0 auto;
}
.sp-dl dd {
    width: 80%;
    margin: 0 auto;
}
.sp-dl dd h5{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    text-align: center;
    font-size: 14px;
}
.sp-dl dd p{
	font-size: 14px;
}
h4{
   text-align:left
}
.package{
  background:#FFFFFF;
  padding: 5px 0;
  border-right: none;
  border-left: none;
}
.footter{
  width: 100%;
  height: 45px;
  position: fixed;
  border-top: 1px solid #cccccc;
  left: 0px;
  bottom: 0px;
  background: #f8f8f8;
}
.sp-content{
  padding-bottom: 45px;
}
.goBtn{
    width: 100px;
    background: #F99800;
    line-height: 45px;
    color: #fff;
}
.vipBox{
   font-weight: bolder;
   font-size: 18px;
   color: red;
}
.imgLabel{
  display: block;
  width: 24px;
  height: 24px;
  margin-left: 10px;
}
.imgLabel img{
  border-radius: 100%;
}
.imgLabel+span{
  margin-left: 15px;
}
.Arrow{
   background: url('../../../static/images/Arrow.png') no-repeat right center;
   background-size: 8px auto;
   margin-right: 10px;
   padding-right: 15px;
}
.evaluate{
   padding: 10px;
}
.evaluate h2{
     font-weight: bolder;
     font-size: 16px;
}



